.App {
  height: 100%;
  margin: 0;
  text-align: center;
  color: rgb(236, 236, 245);
  background-color: black;
}


html {
  height: 100%;
  margin: 0;
}
/* Flexbox; source: https://stackoverflow.com/a/37502323/3833166 */
.grid {
  display: flex; /* establish flex container */
  flex-wrap: wrap; /* enable flex items to wrap */
  justify-content: space-around;
  height: 100%;
  margin: 0;
  align-items: stretch;
}
.cell {
  /* don't grow, don't shrink, width */
  flex: 0 0 23%;
  font-size: 200%;
  margin: 1%;
  background-color: rgb(44, 160, 26);
}
.cell:nth-child(2n) {
  background-color: rgb(0, 30, 255);
}
